.create-account-container {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  height: 75vh;
  background-color: #f5f5f5;
}

.create-account-container h1 {
  color: #333;
  margin-bottom: 20px;
}

.create-account-card {
  width: 100%;
  max-width: 400px;
  padding: 30px;
  background-color: white;
  border-radius: 8px;
  box-shadow: 0 4px 6px rgba(0, 0, 0, 0.1);
}

.create-account-card label {
  display: block;
  margin-bottom: 15px;
  color: #555;
}

.create-account-card input {
  width: 100%;
  padding: 7px;
  margin-top: 5px;
  border: 1px solid #ddd;
  border-radius: 4px;
  font-size: 16px;
}

.create-account-card button {
  width: 100%;
  padding: 10px;
  background-color: #007bff;
  color: white;
  border: none;
  border-radius: 4px;
  font-size: 20px;
  cursor: pointer;
  transition: background-color 0.3s ease;
}

.create-account-card button:hover {
  background-color: #0056b3;
}

.error {
  color: #dc3545;
  margin-bottom: 15px;
}

.sign-up {
  display: block;
  margin-top: 20px;
  text-align: center;
  color: #007bff;
  text-decoration: none;
}

.sign-up:hover {
  text-decoration: underline;
}